
.main_game{
    height: 98%;
 max-height: 500px;
 width: 98%;
 max-width: 600px;
    background: rgb(184, 184, 224);
}

.input_button{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    background: url("https://freefrontend.com/assets/img/css-background-patterns/css-lattice-pattern.png");
    

}
.input_button input {
   margin: 5% 0;
   display: flex;
   flex-direction: row-reverse;
}
.input_button input{
    width: 300px;
    text-align: center;
}
.input_button button{
   padding: 4% 8%;
   background: red;
}
.input_button button:hover{
    color: white;
    cursor: pointer;
}