
.global_win{
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  height:100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 12;
  background: rgba(0, 0, 0, 0.568);
}


.in{
    position: relative;
    height: 20vh;
    width: 20vw;
    background: url("https://static.ngs.ru/news/99/preview/c837e4fb3b697fd8bf589977bbd2688e7c600f3c_867_578_c.jpg.webp-portal"); 
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 25px;
}
.b{
    position: absolute;
    top:0;
    right: 0;
    background: red;
    height: 30px;
    width: 30px;
    font-size: 22px;
    opacity: 0.7;
    transition: all .5s;
}
.b:hover{
    opacity: 1;
    cursor: pointer;
height:40px ;
width: 40px;
}