.global_empty{
    position: fixed;
    top: 0;
    left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.705);
  display: flex;
  justify-content: center;
  align-items: center;

}
.empty{
    background: rgba(163, 163, 163, 0.644);
    color: white;
    padding: 20px 24px;
    transition:all  .4s;
    opacity: .7;
    cursor: pointer;
   

}
.empty:hover{
    opacity: 1;
    color: rgb(255, 255, 227);
    box-shadow: 0px 0px 7px white ; 
}