.game_div{
    width: 100%;
    height: 100%;
    display: flex;
}

.game{
    align-items: center;
    width: 70%;
    background: blueviolet;
    display: flex;
    flex-wrap: wrap;
    max-width: 650px;
    justify-content: space-between;
     height: 100%;
}
.game div{
    background: red;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 21vw;
    border: 1px solid rgb(0, 0, 0);
    height: 21vw;
   max-width: 135px;
   max-height: 135px;
    opacity: 0.5;
    font-size: 50px;
}
.game div:hover{
    opacity: 1;
    border: 1px solid rgb(0, 0, 0);
   }

   .players{
    position: relative;
    width: 30%;
}

.current_player{
    display: flex;
    justify-content: center;
    padding: 25px 10px;
    border: 2px solid rgb(184, 180, 180);

    top: 0;
    right: 20px;
    font-weight: bold;
    font-size: 25px;
  
}

.back_position{
    position: absolute;
    padding: 5px;
    background: green;
}

.back_position:hover{
    font-weight: bold;
    cursor: pointer;
}


.global_inform_players{
    background: red;
  
    position:absolute;
    bottom: 20%;
    width:100%;
  
    display: flex;
    
    justify-content:center;
    align-items: center;
    border-top:2px solid black ;
    border-bottom: 2px solid black;
}

.firts_player{
    padding-right: 10%;
    border-right: 1px solid black;

}
.name{
    font-weight: bold;  
}
.win{
    font-weight: bold;  
}
.step{
    font-weight: bold;  
}
.second_player{
padding-left: 10%;
    border-left: 1px solid black;
}
.reset{
    position: absolute;
    bottom: 0;
    width: 100%;   
}

.reset button{
    display: flex;
    justify-content: center;
    background: red;
    width: 100%;
    color: white;
}

.reset button:hover{
    font-weight: bold;
    cursor: pointer;
}


.back_position{
    width: 100%;
    color: wheat;
}


.game div.win {
    position: relative;
    opacity: 1;
}
.game div.win::after {
    content: " ";
    position: absolute;
  
    z-index: 10;
    width: 239%;
    border:2px solid rgb(5, 5, 5);
    transform: rotate(115deg);
    display: flex;
    justify-content: center;
    align-items: center;
  
}
.game div.win_ver::after{
    transform: rotate(90deg);
    width: 30vw;
}

.game div.win_line::after{
    transform: rotate(180deg);
    width: 100%;
}

.game div.win_dia_left::after{
    transform: rotateZ(50deg);
    width: 30vw;
   
}
.game div.win_dia_right::after{
    transform: rotate(130deg);
    width: 30vw;
   
}

@media screen and (max-width:600px) {
    .global_inform_players{
        flex-direction: column;
        font-size: 15px;
       
    }
    .current_player{
        font-size: 15px;
    }
    .firts_player{
       padding: 0 0 0 6px;
        border-right:none;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        border-bottom: 1px solid black;
      
        
    }
    .second_player{
        padding: 0 6px 0 0;
        border-left: none;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
         width: 100%;   
    }

}